<style scoped>
</style>

<template>
  <el-dialog :visible.sync="visible" title="">
    <div id="printJS-iframe">
      <div class="head">
        <h3 style="text-align: center"> </h3>
        <div style="display: flex;justify-content: space-between">
          <span>:{{orderDomain ? orderDomain.name : ''}}</span>
          <span>:{{orderDomain ? orderDomain.purchasedDate : ''}}</span>
          <span>:{{orderDomain ? orderDomain.no : ''}}</span>
        </div>
      </div>
      <table class="center" border="1px" cellpadding="0" cellspacing="0" style="min-width: 100%;border-collapse:collapse;">
        <tr>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
        </tr>
        <tr v-for="commodity in orderDomain.equipmentDetailList">
          <td>{{commodity.no}}</td>
          <td>{{commodity.name}}</td>
          <td>{{commodity.manufacturer}}</td>
          <td>{{commodity.number}}</td>
          <td>{{commodity.price}}</td>
          <td>{{commodity.money}}</td>
          <td>{{commodity.remark}}</td>
        </tr>
        <tr>
          <td></td>
          <td>{{swapper(orderDomain.money)}}</td>
          <td></td>
          <td>{{orderDomain ? orderDomain.number : ''}}</td>
          <td></td>
          <td colspan="2">{{orderDomain ? orderDomain.money : ''}}</td>
        </tr>
        <tr>
          <td></td>
          <td>{{orderDomain ? orderDomain.manufacturer : ''}}</td>
          <td></td>
          <td colspan="4">{{orderDomain ? orderDomain.addr : ''}}</td>
        </tr>
      </table>
      <div class="footer">
        <span style="margin-right: 20px">:{{orderDomain ? orderDomain.making : ''}}</span>
        <span style="margin-right: 20px">:{{orderDomain ? orderDomain.handle : ''}}</span>
        <span style="margin-right: 20px">:{{this.$moment(new Date()).format('YYYY/MM/DD HH:mm:ss')}}</span>
        <span>:</span>
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="visible = false"></el-button>
      <el-button type="primary" @click="onAdd"></el-button>
    </div>
  </el-dialog>
</template>


<script>
  import printJS from 'print-js'

  export default {
    name: 'OutboundMd',
    data () {
      return {
        visible: false,
        orderDomain: null
      }
    },
    methods: {
      open (orderDomain) {
        this.orderDomain = this.$lodash.merge({}, orderDomain)
        console.log(this.orderDomain.id)
        this.visible = true
        let _this = this
        this.$http.get('/api/OutboundAction.action?orderBy=id&totalCount=&pageSize=30&pageNo=0&id=' + orderDomain.id).then((response) => {
          _this.orderDomain = response.body.success ? response.body.data : {}
          console.log(_this.orderDomain)
        })
      },
      onAdd () {
        let _this = this
        let promise = new Promise(function(resolve, reject) {
          printJS({
            printable: 'printJS-iframe', type: 'html',
            scanStyles: false
          })
          resolve()
        })
        promise.then(function() {
          setTimeout(() => {
            _this.visible = false
            console.log(_this.visible)
            _this.orderDomain = null
          }, 500)
        })
      },
      swapper (n) {
        if (!/^(0|[1-9]\d*)(\.\d+)?$/.test(n))
          return "";
        var unit = "", str = "";
        n += "00";
        var p = n.indexOf('.');
        if (p >= 0)
          n = n.substring(0, p) + n.substr(p + 1, 2);
        unit = unit.substr(unit.length - n.length);
        for (var i = 0; i < n.length; i++)
          str += ''.charAt(n.charAt(i)) + unit.charAt(i);
        return str.replace(/(|||)/g, "").replace(/()+/g, "").replace(/(||)/g, "$1").replace(/()|()/g, "$1$2").replace(/^?|/g, "").replace(/$/g, "");
      }
    }
  }
</script>



